import { TextField, Box } from "@mui/material";

const InputBuscar = ({ setDireccion }) => {
  const changeOnchange = (e) => {
    setDireccion(
      "https://rickandmortyapi.com/api/character/?name=" +
        e.target.value
    );
  };
  return (
    <Box
      sx={{
        background: "white",
        margin: "0.3rem",
        borderRadius: "0.5rem",
        minWidth: "100%"
      }}
    >
      <TextField
        fullWidth
        label="Buscar"
        size="small"
        margin="dense"
        onChange={(e) => changeOnchange(e)}
      />
    </Box>
  );
};

export default InputBuscar;
