import { Box, Alert } from "@mui/material";
import InputBuscar from "../elements/InputBuscar";
import { useState, useEffect } from "react";
import Cards from "./Cards";
import consultaFetch from "../funcionesEspeciales/consultaFetch";

const PruebaEstructura = () => {
  const styless = {
    display: "flex",
    flexDirection: "column",
    color: "white",
    background: "#bdbdbd",
    borderRadius: "0.5rem",
    padding: "0.5rem"
  };

  const [direccion, setDireccion] = useState(
    "https://rickandmortyapi.com/api/character"
  );
  const [listCards, setListCards] = useState([]);

  useEffect(() => {
    consultaFetch(direccion).then((element) => {
      let arr = [];
      element.map((e) => arr.push(e));
      setListCards(arr);
      arr.length > 0 &&
        localStorage.setItem("direccion", direccion);
    });
  }, [direccion]);

  useEffect(() => {
    localStorage.getItem("direccion") &&
      setDireccion(localStorage.getItem("direccion"));
  }, []);

  return (
    <Box sx={styless}>
      <Box
        sx={{
          background: "#212121",
          borderTopLeftRadius: "0.5rem",
          borderTopRightRadius: "0.5rem",
          borderBottom: "10px solid #7c4dff",
          display: "flex",
          flexDirection: "columns",
          alignItems: "center",
          justifyContent: "center",
          padding: "0.5rem"
        }}
      >
        <InputBuscar setDireccion={setDireccion} />
      </Box>

      <Box
        sx={{
          background: "white",
          margin: "0.3rem",
          borderRadius: "0.5rem",
          display: "flex",
          flexWrap: "wrap",
          justifyContent: "space-around"
        }}
      >
        {listCards.length > 0 ? (
          listCards.map((e) => (
            <Cards
              key={e.id}
              name={e.name}
              species={e.species}
              image={e.image}
            />
          ))
        ) : (
          <Alert severity="warning">
            No se encuentra la busqueda â€” No found!
          </Alert>
        )}
      </Box>
    </Box>
  );
};

export default PruebaEstructura;
