const consultaFetch = async (direccion) => {
  let arr = [];
  await fetch(direccion)
    .then((data) => data.json())
    .then((resp) => {
      if (resp.results) {
        resp.results.forEach((element) =>
          arr.push(element)
        );
      }
    });
  return arr;
};

export default consultaFetch;
